@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:ital,wght@1,300&family=Dancing+Script:wght@600&family=Libre+Baskerville&family=Oswald:wght@500&family=Poppins:wght@200;900&family=Raleway:wght@500&family=Roboto&family=Teko:wght@700&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-repeat: none;
}
html{
    font-family: 'Poppins', sans-serif;
    font-size: 62.5%;
}
body{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #252839;
    text-transform: uppercase;
}
h2{
    position: relative;
    font-size: 12rem;
    color: #252839;
    -webkit-text-stroke:0.3rem #383d52;
    
}
h2::before{
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width:0;
    height: 100%;
    color: #01fe87;
    border-right: 0.4rem solid  #01fe87;
    -webkit-text-stroke:0rem #383d52;
    overflow: hidden;
    animation: animate 6s linear infinite;
}

@keyframes animate{
    0%,10%,100%{
         width: 0%;
    }
    70%,80%{
         width: 100%;
    }
}
@media only screen and (max-width:980px){
    html{
        font-size: 55%;
    }
}
@media only screen and (max-width:780px){
    html{
        font-size: 45%;
    }
}
@media only screen and (max-width:498px){
    html{
        font-size: 35%;
    }
}
@media only screen and (max-width:390px){
    html{
        font-size: 25%;
    }
}